import React, { useState } from 'react';
import { styled } from '@mui/material/styles';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import {
  Button,
  Typography,
  Table,
  TableContainer,
  TableBody,
  TableRow,
  Paper,
  Zoom,
} from '@mui/material';
import Feature from '../feature/Feature';
import Modal from '../modal/Modal';
import { RESET_WARNING, RESULTS_ANNOUNCE } from '../../common/staticData';

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.body}`]: {
    fontSize: 14,
    color: theme.palette.common.white,
    backgroundColor: '#2f557b',
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  '&:nth-of-type(odd)': {
    backgroundColor: theme.palette.action.hover,
  },
  'td, th': {
    height: '2.6rem',
  },
  // hide last border
  '&:last-child td, &:last-child th': {
    border: 0,
  },
}));

const resetStyles = {
  alignItems: 'center',
  display: 'flex',
  justifyContent: 'space-between',
  marginTop: '1.6rem',
  width: '100%',
};

const btnStyles = {
  background: 'var(--gradient-bar)',
  color: 'var(--gradient-text)',
};

const CustomTable = ({ data = [], handleReset = () => { } }) => {
  const [open, setOpen] = useState(false);

  const openModal = () => setOpen(true);

  return (
    <Zoom in timeout={300}>
      <div>
        <Feature title={RESULTS_ANNOUNCE.title} text={RESULTS_ANNOUNCE.description} />
        <TableContainer component={Paper} sx={{ marginTop: '1.1rem' }}>
          <Table size="small" aria-label="simple table" sx={{ padding: '1rem' }}>
            <TableBody>
              {data.map(({ key, label, value }) => (
                <StyledTableRow
                  key={key}
                >
                  <StyledTableCell align="left">{label}</StyledTableCell>
                  <StyledTableCell align="left">{value}</StyledTableCell>
                </StyledTableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
        <div style={resetStyles}>
          <Typography
            style={{ color: 'var(--color-text)' }}
          >
            Click the Reset button to upload again.
          </Typography>
          <Button
            variant="contained"
            component="span"
            size="small"
            style={btnStyles}
            onClick={openModal}
          >
            Reset
          </Button>
        </div>
        <Modal
          open={open}
          setOpen={setOpen}
          buttonLabel="Reset"
          message={RESET_WARNING}
          handleConfirm={handleReset}
        />
      </div>
    </Zoom>
  );
};

export default CustomTable;
