import React, { useState } from 'react';
import { RiMenu3Line, RiCloseLine } from 'react-icons/ri';
import { JAIRGLogo } from '../../components';
import { NAV_LINKS } from '../../common/staticData';
import './navbar.css';

const Navbar = () => {
  const [toggleMenu, setToggleMenu] = useState(false);

  const openNavBarMenu = () => { setToggleMenu(true); };
  const closeNavBarMenu = () => { setToggleMenu(false); };

  const renderNavLinks = () => (
    <>
      {NAV_LINKS.map(({ label, href }) => (
        <p className="link-text" key={`key_${label}`}>
          <a onClick={closeNavBarMenu} href={href}>{label}</a>
        </p>
      ))}
    </>
  );

  return (
    <div className="jairg__navbar">
      <div className="jairg__navbar-links">
        <JAIRGLogo />
        <div className="jairg__navbar-links_container">
          {renderNavLinks()}
        </div>
      </div>
      <div className="jairg__navbar-menu">
        {toggleMenu
          ? <RiCloseLine color="#fff" size={27} onClick={closeNavBarMenu} />
          : <RiMenu3Line color="#fff" size={27} onClick={openNavBarMenu} />}
        {toggleMenu && (
        <div className="jairg__navbar-menu_container scale-up-center">
          <div className="jairg__navbar-menu_container-links">
            {renderNavLinks()}
          </div>
        </div>
        )}
      </div>
    </div>
  );
};

export default Navbar;
