import React from 'react';
import axios from 'axios';
import { IconButton } from '@mui/material';
import DownloadIcon from '@mui/icons-material/CloudDownload';

export const BACKEND_API = 'http://localhost:8000';

const formatSizeUnits = (bytes) => {
  let result;
  if (bytes >= 1073741824) {
    result = `${(bytes / 1073741824).toFixed(2)}G`;
  } else if (bytes >= 1048576) {
    result = `${(bytes / 1048576).toFixed(2)}M`;
  } else if (bytes >= 1024) {
    result = `${(bytes / 1024).toFixed(2)}K`;
  } else if (bytes >= 1) {
    result = `${bytes}B`;
  } else {
    result = '0B';
  }
  return result;
};

const formatTime = (seconds) => {
  if (seconds != null) {
    const result = new Date(seconds * 1000).toISOString().slice(11, 19);
    return result;
  }
  return null;
};

const emailValidation = (inputEmail) => {
  const regEx = /[a-zA-Z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,8}(.[a-z{2,8}])?/g;
  return regEx.test(inputEmail);
};

const fileExtensionValidation = (file) => {
  const validExtension = 'fastq.gz';
  return file.name.slice(-8) === validExtension;
};

const fetchEstimateTimeForFileProcessing = async (fileSize) => {
  const estimateTimeAPI = `${BACKEND_API}/estimate_time`;
  let inputFileSize = formatSizeUnits(fileSize * 3);
  inputFileSize = `${Math.trunc(Number(inputFileSize.slice(0, -1)))}${inputFileSize.slice(-1)}`;
  const data = JSON.stringify({ file_size: inputFileSize });
  try {
    const response = await axios.post(estimateTimeAPI, data);
    const estimateTime = await response.data;
    return estimateTime;
  } catch (error) {
    return error;
  }
};

const fetchPredictionResults = async (inputFile, email) => {
  const predictionModalAPI = `${BACKEND_API}/classify`;
  if (inputFile) {
    const formData = new FormData();
    formData.append('gz_file', inputFile, inputFile.name);
    if (email) formData.append('email', email);
    try {
      const requestProps = [
        predictionModalAPI,
        formData,
      ];
      const response = await axios.post(...requestProps);
      const predictionResults = await response.data;
      return predictionResults;
    } catch (error) {
      return error;
    }
  }
  return null;
};

const formatEstimates = (estimates) => [
  {
    key: 'e1',
    label: 'Minimum Time',
    value: formatTime(estimates.min_time),
  },
  {
    key: 'e2',
    label: 'Maximum Time',
    value: formatTime(estimates.max_time),
  },
];

const formatResponse = (response/* , inputFile */) => {
  const downloadFile = () => {
    if (response.features_csv) {
      let downloadLink = response.features_csv.split('/');
      const index = downloadLink.indexOf('media');
      downloadLink = downloadLink.slice(index).join('/');
      downloadLink = `${BACKEND_API}/${downloadLink}`;
      window.open(downloadLink);
      // document.location.href = downloadLink;
    }
  };

  return [
    {
      key: 'r1',
      label: 'Input File',
      value: response.input_file_name,
    },
    {
      key: 'r2',
      label: 'Input File Size',
      value: `${response.input_file_size}B`,
    },
    {
      key: 'r3',
      label: 'Main Class',
      value: response.main_class,
    },
    {
      key: 'r4',
      label: 'Sub Class',
      value: response.sub_class,
    },
    {
      key: 'r5',
      label: 'Confidence',
      value: `${response.confidence}%`,
    },
    {
      key: 'r6',
      label: 'Features File (.csv)',
      value: response.features_csv
        && (
          <IconButton
            size="small"
            onClick={downloadFile}
          >
            <DownloadIcon fontSize="medium" style={{ color: 'white' }} />
          </IconButton>
        ),
    },
  ];
};

export {
  emailValidation,
  fetchEstimateTimeForFileProcessing,
  fetchPredictionResults,
  fileExtensionValidation,
  formatEstimates,
  formatResponse,
  formatSizeUnits,
  formatTime,
};
