import React from 'react';
import { logo } from '../../assets/images';
import './JAIRGLogo.css';

const openWebSite = () => {
  window.open('https://www.suraiyajabin.in/');
};

const JAIRGLogo = () => (
  <div onClick={openWebSite} className="jairg_logo_container">
    <img draggable="false" className="jairg_logo_img" src={logo} alt="logo" />
    <div className="jairg_logo">
      <h1>JAIRG</h1>
      <span className="jairg_logo_bottom">
        Jamia Artificial Intelligence Research Group
      </span>
    </div>
  </div>
);

export default JAIRGLogo;
