import React from 'react';
import { Button, Container, TextField, Zoom } from '@mui/material';
import { LoadingButton } from '@mui/lab';
import Feature from '../feature/Feature';
import { ACCEPTED_FILE_FORMATE } from '../../common/staticData';
import { Input, Margin, btnStyles, containerStyles } from './fileUploadForm-styles';

const FileUploadForm = (props) => {
  const {
    email,
    isEmailValid,
    handleEmailInputChange,
    inputFile,
    isFileValid,
    handleFileInputChange,
    isLoading,
    isSubmitBtnDisabled,
    handleSubmit,
  } = props;
  const emailInputHelperText = email !== '' && !isEmailValid
    ? 'Please enter a valid email!'
    : "We won't share your email anywhere.";
  const fileInputHelperText = !inputFile?.name || isFileValid
    ? '*.fastq.gz'
    : 'Please choose a valid file!';

  return (
    <Zoom in timeout={400}>
      <div>
        <Feature
          title={ACCEPTED_FILE_FORMATE.title}
          text={ACCEPTED_FILE_FORMATE.description}
        />
        <Margin />
        <Container
          maxWidth="sm"
          sx={containerStyles}
        >
          <TextField
            size="small"
            error={email !== '' && !isEmailValid}
            focused={!!email}
            fullWidth
            id="outlined-required"
            label="Email (Optional)"
            helperText={emailInputHelperText}
            value={email}
            onChange={handleEmailInputChange}
            color={isEmailValid ? 'success' : 'primary'}
            autoComplete="off"
          />
          <Margin />
          <div className="file-input">
            <TextField
              focused={!!inputFile?.name}
              size="small"
              error={inputFile?.name && !isFileValid}
              fullWidth
              // disabled={!inputFile?.name}
              id="outlined-required"
              label={inputFile?.name ? 'File' : 'No file chosen'}
              helperText={fileInputHelperText}
              value={inputFile?.name || ''}
              color={isFileValid ? 'success' : 'primary'}
            />
            <label htmlFor="outlined-button-file">
              <Input
                accept="file"
                id="outlined-button-file"
                type="file"
                variant="outlined"
                onChange={handleFileInputChange}
              />
              <Button
                variant="contained"
                component="span"
                size="large"
                style={{ ...btnStyles, marginLeft: '.4rem' }}
              >
                Upload
              </Button>
            </label>
          </div>
          <Margin />
          <LoadingButton
            variant="contained"
            component="span"
            size="large"
            style={!isSubmitBtnDisabled ? btnStyles : {}}
            disabled={isSubmitBtnDisabled}
            fullWidth
            onClick={handleSubmit}
            loading={isLoading}
            loadingPosition="start"
            startIcon={<></>}
          >
            Submit
          </LoadingButton>
        </Container>
      </div>
    </Zoom>
  );
};

export default FileUploadForm;
