import React, { useState, useEffect } from 'react';
import {
  formatEstimates,
  formatResponse,
  fetchPredictionResults,
  fetchEstimateTimeForFileProcessing,
  fileExtensionValidation,
  emailValidation,
} from '../../common/utils';
import { agtcImage } from '../../assets/images';
import { CustomTable, Countdown, Error, Loader, FileUploadForm } from '../../components';
import { FORM_HEADING } from '../../common/staticData';
import './form.css';

const Form = () => {
  const [isLoading, setIsLoading] = useState(false);
  const [showForm, setShowForm] = useState(true);
  const [estimateTime, setEstimateTime] = useState(null);
  const [countdown, setCountdown] = useState(0);
  const [inputFile, setInputFile] = useState(null);
  const [isFileValid, setIsFileValid] = useState(false);
  const [email, setEmail] = useState('');
  const [isEmailValid, setIsEmailValid] = useState(false);
  const [response, setResponse] = useState(null);
  const [error, setError] = useState(false);
  const isSubmitBtnDisabled = ((email !== '' && !isEmailValid) || !isFileValid) || isLoading;

  const handleFileInputChange = (event) => {
    event.preventDefault();
    const { target: { files } } = event;
    setInputFile(files[0]);
    setIsFileValid(fileExtensionValidation(files[0]));
  };

  const handleEmailInputChange = (event) => {
    event.preventDefault();
    const { target: { value } } = event;
    setEmail(value);
    setIsEmailValid(emailValidation(email));
  };

  const handleSubmit = async (event) => {
    event.preventDefault();
    if (inputFile && isFileValid) {
      setIsLoading(true);
      setShowForm(false);
      try {
        try {
          const fetchEstimateTime = await fetchEstimateTimeForFileProcessing(inputFile.size);
          setCountdown(fetchEstimateTime.max_time);
          setEstimateTime(formatEstimates(fetchEstimateTime));
          setError(false);
        } catch (err) {
          setError(err);
        }
        let predictionResults;
        if (email && isEmailValid) {
          predictionResults = await fetchPredictionResults(inputFile, email);
        } else {
          predictionResults = await fetchPredictionResults(inputFile);
        }
        setIsLoading(() => {
          setResponse(formatResponse(predictionResults));
          setError(false);
          return false;
        });
        if (predictionResults && predictionResults.main_class) {
          setError(false);
        } else setError(true);
      } catch (err) {
        setIsLoading(false);
        setError(err);
      }
    }
    return null;
  };

  const handleReset = () => {
    setError(null);
    setIsLoading(false);
    setEstimateTime(null);
    setCountdown(0);
    setInputFile(null);
    setIsFileValid(false);
    setEmail('');
    setIsEmailValid(false);
    setResponse(null);
    setShowForm(true);
  };

  const handleCancelRequest = async () => {
    handleReset();
  };

  const handleRendering = () => {
    let result;
    if (error) {
      result = <Error handleBack={handleReset} />;
    } else if (showForm && !isLoading) {
      result = (
        <FileUploadForm
          email={email}
          isEmailValid={isEmailValid}
          handleEmailInputChange={handleEmailInputChange}
          inputFile={inputFile}
          isFileValid={isFileValid}
          handleFileInputChange={handleFileInputChange}
          isLoading={isLoading}
          isSubmitBtnDisabled={isSubmitBtnDisabled}
          handleSubmit={handleSubmit}
        />
      );
    } else if (estimateTime && countdown && isLoading && !error) {
      result = (
        <Countdown
          time={countdown}
          handleCancelRequest={handleCancelRequest}
        />
      );
    } else if (response && !isLoading && !error) {
      result = (
        <CustomTable
          data={response}
          handleReset={handleReset}
        />
      );
    } else {
      result = <Loader />;
    }
    return result;
  };

  // const handleTabClosing = () => {
  //   request.cancel('Tab closed! Request aborted.');
  // };

  // const alertUser = (event) => {
  //   event.preventDefault();
  //   event.returnValue = '';
  // };

  const preventDefault = (event) => {
    event.preventDefault();
  };

  useEffect(() => {
    // window.addEventListener('beforeunload', alertUser);
    // window.addEventListener('unload', handleTabClosing);
    window.addEventListener('contextmenu', preventDefault);
    window.addEventListener('dragstart', preventDefault);
    window.addEventListener('drop', preventDefault);

    return () => {
      // window.removeEventListener('beforeunload', alertUser);
      // window.removeEventListener('unload', handleTabClosing);
      window.removeEventListener('contextmenu', preventDefault);
      window.removeEventListener('dragstart', preventDefault);
      window.removeEventListener('drop', preventDefault);
    };
  }, []);

  return (
    <div id="upload-file" className="section__padding">
      <div className="jairg__form">
        <div className="jairg__form-heading">
          <h1 className="gradient__text">{FORM_HEADING}</h1>
          <img draggable="false" className="agtc-img" src={agtcImage} alt="agtc" />
        </div>
        <div className="jairg__form-container">
          {handleRendering()}
        </div>
      </div>
    </div>
  );
};

export default Form;
