import React from 'react';
import './feature.css';

const renderPeople = (people) => {
  const person = ({ name, linkedin }, i, { length }) => (
    <span key={`key_${name}`}>
      <a className="profile-link" href={linkedin} target="blank">{name}</a>
      {i === length - 2 && length > 1 ? ' and ' : i !== length - 1 && ', '}
    </span>
  );
  return people.map(person);
};

const People = ({ title, people }) => (
  <div className="jairg__features-container__feature">
    <div className="jairg__features-container__feature-title">
      <div />
      <h1>{title}</h1>
    </div>
    <div className="jairg__features-container_feature-text">
      <p>{renderPeople(people)}</p>
    </div>
  </div>
);

export default People;
