import React from 'react';
import { People } from '../../components';
import { contributionImage } from '../../assets/images';
import { CONTRIBUTORS_DATA } from '../../common/staticData';
import './contributors.css';

const renderContributorsList = () => CONTRIBUTORS_DATA.map((item, index) => (
  <People
    key={`key_${item.title}${index}`}
    title={item.title}
    people={item.contributors}
    text={item.text}
  />
));

const Contributors = () => (
  <div className="jairg__contributors section__padding" id="info">
    <div className="jairg__contribution-banner-container">
      <img draggable="false" className="contribution-banner" src={contributionImage} alt="" />
    </div>
    <div className="jairg__contributors-container">
      {renderContributorsList()}
    </div>
  </div>
);

export default Contributors;
