export const OVERVIEW_SECTION_DATA = {
  title: 'Overview',
  description: 'This dynamic server enables the user to upload the clinical data in form of zipped SRA fastq files and predict their label into seven categories of bacterial family viz. Escherichia coli, Staphylococcus,  Klebsiella, Acinetobacter, Pseudomonas aeruginosa, Enterococcus, and Enterobacter. FASTQ format is a text-based format for storing both a biological sequence (usually nucleotide sequence) and its corresponding quality scores. Both the sequence letter and quality score are each encoded with a single ASCII character for brevity. It contains the sequence data from the clusters that pass filters on a flow cell. If samples were multiplexed, the first step in FASTQ file generation is demultiplexing. The size of a bacterial SRA zipped fastq file ranges between 5 MB to 30 GB. This is a dynamic server and it can handle multiple requests at the same time. The first step of the designed web server is to turn fastq sequences into a feature vector with a count of 2000 kmer sequences. These kmer sequences are the most frequently occurring subsequences. At the backend of this server, we have an ensemble of 30 machine learning models, which classify this feature vector into these 7 categories using majority voting.',
  end: 'Start uploading your file below and wait for the prediction results',
};

