import React, { useState } from 'react';
import { Button, Typography, styled, Zoom } from '@mui/material';
import { CountdownCircleTimer } from 'react-countdown-circle-timer';
import Feature from '../feature/Feature';
import Modal from '../modal/Modal';
import { PROCESSING_REQUEST, STOP_PROCESSING_WARNING } from '../../common/staticData';
import './countdown.css';

const Margin = styled('div')({
  marginBottom: '2rem',
});

const btnStyles = {
  background: 'var(--gradient-bar)',
  color: 'var(--gradient-text)',
};

const Countdown = (props) => {
  const { time = 60, handleCancelRequest = () => { } } = props;
  const [open, setOpen] = useState(false);

  const openModal = () => setOpen(true);

  const renderTime = ({ remainingTime }) => (
    <div className="timer">
      <div className="text">HH:MM:SS</div>
      <h1 className="value gradient__text">
        {new Date(remainingTime * 1000).toISOString().slice(11, 19)}
      </h1>
      <div className="text">Countdown</div>
    </div>
  );
  return (
    <Zoom in timeout={300}>
      <div className="countdown-wrapper">
        <Feature
          title={PROCESSING_REQUEST.title}
          text={PROCESSING_REQUEST.description}
        />
        <Margin />
        <CountdownCircleTimer
          isPlaying
          duration={time}
          colors={['#81AFDD', '#AE67FA', '#F49867']}
          isLinearGradient
          // onComplete={() => ({ shouldRepeat: true, delay: 2 })}
          size={230}
          strokeWidth={8}
          trailColor="transparent"
        >
          {renderTime}
        </CountdownCircleTimer>
        <Margin />
        <div className="stop-wrapper">
          <Typography
            style={{ color: 'var(--color-text)' }}
          >
            Click the Stop button to stop the processing.
          </Typography>
          <Button
            variant="contained"
            component="span"
            size="small"
            style={btnStyles}
            onClick={openModal}
          >
            Stop
          </Button>
        </div>
        <Modal
          open={open}
          setOpen={setOpen}
          buttonLabel="Stop"
          message={STOP_PROCESSING_WARNING}
          handleConfirm={handleCancelRequest}
        />
      </div>
    </Zoom>
  );
};

export default Countdown;
