import React from 'react';
import ErrorIcon from '@mui/icons-material/Error';
import { Box, Typography, Button, Zoom } from '@mui/material';
import Feature from '../feature/Feature';
import { FAILED_REQUEST_ERROR } from '../../common/staticData';

const resetStyles = {
  alignItems: 'center',
  display: 'flex',
  justifyContent: 'space-between',
  marginTop: '1.6rem',
  width: '100%',
};

const btnStyles = {
  background: 'var(--gradient-bar)',
  color: 'var(--gradient-text)',
};

const Error = (props) => {
  const {
    title = FAILED_REQUEST_ERROR.title,
    message = FAILED_REQUEST_ERROR.message,
    handleBack = () => { },
  } = props;

  return (
    <Zoom in timeout={300}>
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'center',
        }}
      >
        <Feature title={title} text={message} />
        <ErrorIcon style={{ color: '#2f557b', fontSize: '18rem' }} />
        <div style={resetStyles}>
          <Typography
            style={{ color: 'var(--color-text)' }}
          >
            Click the Back button to go back to the main form.
          </Typography>
          <Button
            variant="contained"
            component="span"
            size="small"
            style={btnStyles}
            onClick={handleBack}
          >
            Back
          </Button>
        </div>
      </Box>
    </Zoom>
  );
};

export default Error;
